.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0,0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;

  &__content {
    background-color: #fff;
    border-radius: 3px;
    max-height: 95vh;
    overflow-y: auto;
    width: 600px;
    min-width: 320px;

    @media screen and (max-width: 650px) {
      width: 100%;
    }
  }
}